
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
data () {
return {
stock: {}
}
},
methods: {
goBack () {
this.$router.push({
path: '/stockList'
})
},
_getDetail (id) {
this.$tupHttp(this, 'front/stockProduct/getDetail/' + id).then(res => {
this.stock = res.data.model
})
}
},
created () {
this._getDetail(this.$route.query.stockId)
},
components: {
XHeader
}
}



